import React, { useEffect, useState } from 'react';
import api from '../../api'

// import component
import { Table, Button, message, Modal, Input } from 'antd'
const { confirm } = Modal;

const TeacherTable = () => {
   const [users, setUsers] = useState([])
   const [filterword, setFilterWord] = useState("")

   useEffect(() => {
      getData()
   }, [])

   const getData = async () => {
      console.log("getData")
      try {
         let users = await api.user.getUsers();
         // 
         users = users.filter(user => user.permission == "teacher")
         console.log(users)
         setUsers(users)
      } catch (err) {
         message.error("getData err =", err)
      }

   }

   const deleteUser = async (id) => {
      confirm({
         title: '',
         content: '',
         async onOk() {
            try {
               let res = await await api.user.deleteUserById(id)
               message.success("")
               getData()

            } catch (err) {
               message.success(" err = ", err)
            }
         },
         onCancel() {
            console.log('Cancel');
         },
         okText: "",
         cancelText: ""
      });
   }

   const inActiveUser = async (id) => {
      console.log("inActiveUser id = ", id)
      confirm({
         title: '',
         content: '',
         async onOk() {
            try {
               let res = await await api.user.inActiveUser(id)
               message.success("")
               getData()

            } catch (err) {
               message.success(" err = ", err)
            }
         },
         onCancel() {
            console.log('Cancel');
         },
         okText: "",
         cancelText: ""
      });
   }

   const activeUser = async (id) => {
      console.log("activeUser id = ", id)

      confirm({
         title: '',
         content: '',
         async onOk() {
            try {
               let res = await api.user.activeUser(id)
               message.success("")
               getData()

            } catch (err) {
               message.success(" err = ", err)
            }
         },
         onCancel() {
            console.log('Cancel');
         },
         okText: "",
         cancelText: ""
      });
   }
   const filter = (e) => {
      setFilterWord(e.target.value)
   }
   const columns = [
      {
         title: '',
         dataIndex: 'username',
         key: 'username',
      },
      {
         title: '',
         dataIndex: 'name',
         key: 'name',
      },
      {
         title: 'email',
         dataIndex: 'email',
         key: 'email',
      },
      {
         title: '',
         key: 'action',
         render: (text, record) => (
            <div>
               {record.InActive
                  ?
                  <Button type="primary" style={{ marginRight: "1rem" }} onClick={() => activeUser(record._id)}></Button>
                  :
                  <Button style={{ marginRight: "1rem" }} onClick={() => inActiveUser(record._id)}></Button>
               }
               <Button type="danger" onClick={() => deleteUser(record._id)}></Button>
            </div>
         ),
         align: 'right'
      },
   ];

   return (
      <div>
         <h3></h3>
         <h4></h4>
         <Input onChange={filter} placeholder=""></Input>
         <Table columns={columns} dataSource={users.filter(user => user.name.includes(filterword))} />
      </div>
   );
};

export default TeacherTable;